<template lang="html">
  <div class="bottom-bar" :style="{color:color,fontSize:size+'px'}">{{bottomText}}</div>
</template>

<script>
export default {
  name: 'bottomText',

  componentName: 'bottomText',

  props: {
    color: String,
    size: Number
  },

  mounted () {
    this.initBottomText()
  },

  methods: {
    initBottomText () {
      let index = (Math.random() * 10).toFixed(0)
      console.log(index)
      switch (index) {
        case '0':
          this.bottomText = ','
          break
        case '1':
          this.bottomText = ''
          break
        case '2':
          this.bottomText = '?'
          break
        case '3':
          this.bottomText = '!!'
          break
        case '4':
          this.bottomText = ''
          break
        case '5':
          this.bottomText = '?'
          break
        case '6':
          this.bottomText = '?'
          break
        case '7':
          this.bottomText = ''
          break
        case '8':
          this.bottomText = ''
          break
        case '9':
          this.bottomText = '...'
          break
      }
    }
  },

  data () {
    return {
      bottomText: ''
    }
  }
}
</script>

<style lang="css" scoped>
.bottom-bar {
  position: absolute;
  bottom: 5px;
  left: 50%;
  color: #999;
  font-size: 14px;
  transform: translateX(-50%);
}
</style>



